@font-face {
  font-family: "DMSansBold";
  src: url("./fonts/DMSans-Bold.ttf");
  font-weight: bold;
}

@font-face {
  font-family: "DMSansRegular";
  src: url("./fonts/DMSans-Regular.ttf");
  font-weight:normal;
}

.App {
  font-family: "DMSansRegular";
  font-size: 16px;
  line-height: 1.5em;
}

.h1 {
  font-family: "DMSansBold";
  font-size: 40px;
  line-height: 1.2em;
}

.h2 {
  font-family: "DMSansBold";
  font-size: 30px;
  line-height: 1.2em;
}

.App-logo {
  width: 30%;
  min-width: 256px;
  max-width: 324px;
  margin: 10px;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #f0f0f0 ;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: left;
  color: #000000;
}

.shadow{
  box-shadow: 0px 2px 5px #848484;
}

.App-link {
  color: #61dafb;
}

.Content {
  min-height: 85vh;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: left;
  color: #000000;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 5px;
  grid-row-gap: 5px;
  margin: 10px;
}

@media only screen and (max-width: 700px) {
  .Content {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-column-gap: 5px;
    grid-row-gap: 5px;
  }
}

.leads {
  background-color: #c769bd;
  border-radius: 15px;
  margin: 5px;
  padding-left: 10px;
  padding-top: 0px;
  padding-right: 10px;
  padding-bottom: 10px;
}

.meetings {
  background-color: #18ce77;
  border-radius: 15px;
  margin: 5px;
  padding: 5px;
}

.ratio {
  background-color: #26afcd;
  border-radius: 15px;
  margin: 5px;
  padding: 5px;
}

.deals {
  background-color: #c769bd;
  border-radius: 15px;
  margin: 5px;
  padding: 5px;
}

